<template>
	<div>
		<myhead :title='shop.shop_name'>
			<div class="iconfont icon-fanhui left" slot='left' onclick="history.go(-1)"></div>
			<div class="right mui-icon mui-icon-refresh" slot='right'  @click="refresh()"></div>
		</myhead>
		<div class="ophead"></div>
		<div class="shopTitle">
			<div class="imgDiv">
				<img :src="shop_img" />
			</div>
			
			
			<span class="fee">配送费：{{shop.shop_fee}}元</span>
			<span class="act">公告：{{shop.shop_activity}}</span>
		</div>
		
		
		<div class="stabs">
			<div class="item " @click="changeT(1)" :class="currentT==1?'active':''">商品</div>
			<div class="item" @click="changeT(2)" :class="currentT==2?'active':''">餐厅介绍</div>
			<div class="item" @click="changeT(3)" :class="currentT==3?'active':''">待定</div>
		</div>
		
		<!--gun-->
		 <foodGun :shopID='shop_id' @allprice='allprice' v-if="currentT==1" ></foodGun>
		 
		 <div class="bomBar" v-if="currentT==1">
		 	<div class="infos">
		 		共:{{gong}}元
		 	</div>
		 	<div class="pay" @click="purchase">付款</div>
		 </div>
		 <!--介绍-->
		 <div class="intro" v-if="currentT==2">
		 	<div class="item">
		 		<span>商家名称</span>
		 		<span class="thing">{{shop.shop_name}}</span>
		 	</div>
		 	<div class="item">
		 		<span>商家地址</span>
		 		<span class="thing">{{shop.shop_address}}</span>
		 	</div>
		 	<div class="item">
		 		<span>配送费</span>
		 		<span class="thing">{{shop.shop_fee}}元</span>
		 	</div>
		 	<div class="item">
		 		<span>商家拥有人</span>
		 		<span class="thing">{{shop.shop_owner}}</span>
		 	</div>
		 </div>
	</div>
</template>

<script>
	import myhead from '../../components/myhead'
	import {getFoods,turl,addOrder} from '../../api/api'
	import foodGun from '../../components/goodGun'
	export default{
		data(){
			return {
				shop:this.$store.state.shop_store.shop,
				order:[],
				currentT:1,
				gong:0,
			}
		},
		methods:{
			changeT(val){
				this.currentT=val
				
			},
			allprice(...thing){
				this.gong=thing[0].price
				this.order=thing[0].selectedFood
			},
			refresh(){
 
				this.$router.replace('/foodD?shop_id=1')
				  
			},
			async purchase(){
				var tt=this
				if(this.$store.state.person_store.phone==''){
					mui.alert('登录哦，小朋友-.-')
				}
				else if(this.order.length==0||this.order==undefined){
					mui.alert('请选择食品')
				}else{
					
					 var res= await addOrder(JSON.stringify(tt.order),tt.shop_id,tt.gong,this.$store.state.person_store.phone)
					console.log(res)
					if(res.affectedRows==1){
						mui.alert('添加订单成功',function(){
							tt.$router.go(-1)
						})
					}
				}
			}
		},
		computed:{
			shop_id:function(){
				return this.$route.query.shop_id
			},
			shop_img:function(){
				var tt=this
				return turl()[1]+tt.shop.shop_img
			}
		},
		mounted(){
 
			
		},
		components:{
			myhead,foodGun
			
		}
		
	}
</script>

<style scoped>
	.shopTitle{
		background-color: dodgerblue;
		color: white;
		position: relative;
		min-height: 20vw;
	}
	.shopTitle .imgDiv{
		position: relative;
		width: 20vw;overflow: hidden;height: 18vw;margin-left: 2vw;
	}
	.shopTitle img{
		width: 100%;position: absolute;left: 0;top: 0;right: 0;bottom: 0;margin: auto;display: block;
		 
	}
	.shopTitle .fee{
		font-size: 3vw;
		position: absolute;
		left: 24vw;
		top: 12vw;
	}
	.shopTitle .act{
		font-size: 3vw;
		position: absolute;
		left: 24vw;
		top: 4vw;
	}
	.stabs {
		display: flex;
		width: 100vw;
		height: 10vw;
		align-items: center;
		border-bottom: 1px solid gray;
	}
	.stabs .item{
		background-color: white;
		width: 33.33%;
		text-align: center;
		font-size: 4.3vw;
		line-height: 9.8vw;
	}
	.stabs .active{
		 position: relative; transition-duration: 0.2s;
		 animation-duration: 0.2s;
	}
	.stabs .active:after{
		 position: absolute;
		 bottom: 0;
		 transition-duration: 0.2s;
		 animation-duration: 0.2s;
		 left: 0;right: 0;margin: auto;
		 width: 15vw;height: 0.5vw;content: '';background-color: dodgerblue;
	}
	.bomBar{
		background-color: white;position: fixed;bottom: 0;
		display: flex;align-items: center;width: 100vw;line-height: 10vw;
	}
	.bomBar .infos{
		background-color: white;
		 height: 10vw;width: 70%;
		 padding-left: 2vw;line-height: 10vw;
	}
	.bomBar .pay{
		background-color: dodgerblue;color: white;
		height: 10vw;width: 30%;text-align: center;
	}
	.intro {background-color: white;width: 100vw;margin-top: 10px;}
	.intro .item{
		border-top: 1px gainsboro solid;padding: 15px 15px;position: relative;display: flex;
	}
	.intro .item .thing{
		text-align: right;font-size: 14px;color: #444;flex: 1;padding-left: 20vw;
	}
	.mui-icon-refresh{
		line-height: 10vw;font-size: 6vw;
	}
</style>


