<template>
	<div class="person">
		<myhead title='个人'>
			<div class=" left iconfont icon-sousuo" slot='left'>
			</div>
			<!--<div class="right" slot='right'>
				<div>登录</div>
			</div>-->
		</myhead>
		
		<div class="ophead"></div>
		<router-link class="info" to='/login' tag='div' v-if='!$store.state.person_store.is_login'>
			<img src="../../../build/logo.png" class="img-responsive"/>
			<div class="mid">
				<div class="login">登录/注册</div>
				<div class="phone">绑定手机号</div>
			</div>
			<div class="iconfont icon-fanhui fanzhuan">
			</div>
		</router-link>
		
		<div class="info"  v-if='$store.state.person_store.is_login'>
			<img src="../../../build/logo.png" class="img-responsive"/>
			<div class="mid">
				<div class="login">手机号：{{$store.state.person_store.phone}}</div>
				<div class="phone">已登录</div>
			</div>
			<div class="iconfont icon-fanhui fanzhuan">
			</div>
		</div>
		
		<div class="menu3s">
			<div class="item line">
				<div class="h">0.00</div>
				<div class="b">余额</div>
			</div>
			<div class="item line">
				<div class="h">0.00</div>
				<div class="b">余额</div>
			</div>
			<div class="item ">
				<div class="h">0.00</div>
				<div class="b">余额</div>
			</div>
		</div>
		<div style="height: 5px;"></div>
		<div class="list_menu">
			<router-link class="menu" tag='div' to='/order'>
				<div class="mui-icon mui-icon-bars icon"></div>
				<div class="lb">订单</div>
				<div class="arr mui-icon mui-icon-arrowright"></div>
			</router-link>
			<div class="menu" @click="showAddress()">
				<div class="mui-icon mui-icon-location icon"></div>
				<div class="lb">地址管理</div>
				<div class="arr mui-icon mui-icon-arrowright"></div>
			</div>
			<div class="menu" @click="showNN()">
				<div class="mui-icon mui-icon-person icon"></div>
				<div class="lb">修改信息</div>
				<div class="arr mui-icon mui-icon-arrowright"></div>
			</div>
		</div>
		
		<div class="bottomBtn" @click="logout()" v-if='$store.state.person_store.is_login'>
			退出登录
		</div>
		
		<div class="inputDia" v-if="Dialog">
			<div class="mask" @click="Dialog=false"></div>
			<div class="input-group">
				<h2>{{tempD.title}}</h2>
				<div class="mui-input-row">
				    <label>{{tempD.label}}</label>
				    <input type="text" placeholder="昵称" v-model="value">
				</div>
				<button @click="confrim">OK</button>
			</div>
		</div>
	</div>
</template>

<script>
	import myhead from '../../components/myhead'
	import {changeAdd,changeN} from '../../api/api'
	export default {
		components:{
			myhead,
			 
		},
		data(){
			return {
				Dialog:false,
				value:'',
				tempD:{},
				
			}
		},
		methods: {
			 logout:function(){
			 	var person=this.$store.state.person_store
			 	person.is_login=false
			 	person.phone=''
			 	this.$router.replace('/login')	
			 },
			async confrim(){
//			 	this.Dialog=false	
			 	if(this.tempD.type==1){
			 		var res= await changeAdd(this.$store.state.person_store.phone,this.value)
				 	this.Dialog=false
				 	console.log(res,this.$store.state.person_store.phone,this.value)
			 	}else{
			 		var res= await changeN(this.$store.state.person_store.phone,this.value)
				 	this.Dialog=false
				 	console.log(res,this.$store.state.person_store.phone,this.value)
			 	}
			 },
			async showNN(){
			 	
			 	this.tempD={
			 		title:'修改个人昵称',
			 		label:'昵称',
			 		type:2
			 	}
			 	if(this.$store.state.person_store.phone!=''){
			 		this.Dialog=true
			 	}else{
			 		mui.alert('亲，请登录~^.^')
			 	}
			 	
			 },
			 async showAddress(){
//			 	 this.Dialog=true
			 	this.tempD={
			 		title:'更改收货地址',
			 		label:'全地址',
			 		type:1
			 	}
			 	console.log(this.$store.state.person_store.phone)
			 	if(this.$store.state.person_store.phone!=''){
			 		this.Dialog=true
			 	}else{
			 		mui.alert('亲，请登录~^.^')
			 	}
			 	
			 }
			 
		},
		mounted(){
			 
		}
	}
</script>

<style scope>
	.inputDia h2{text-align: center;padding: 2.5vw;border-bottom: 1px solid gainsboro;font-size: 5vw;margin-bottom: 1vw;}
	.inputDia .mask{position: fixed;top: 0;left: 0;width: 100vw;height: 100vh;opacity: 0.7;background-color: black;z-index: 4;}
	.inputDia .input-group{z-index: 5;  border-radius:10px;width: 90vw;height: auto;padding: 10px;position: fixed;top: 30%;left: 0;right: 0;margin: auto;background-color: white;box-shadow: 1px 1px 1px gainsboro;}
	.inputDia .input-group button{width: 100%;display: block;background-color: dodgerblue;color: white;margin-top: 2vw;}
	.person{
		min-height: 100vh;
		background-color: #eaeaea;
	}
	.info{
		display: flex;align-items: center;background-color: dodgerblue;color: white;
	}
	.info img{
		width: 20vw;margin-left: 5vw;
	}
	.info .mid{
		flex: 1;
	}
	.info .mid .login{
		font-size: 4.2vw;margin-bottom: 1.3vw;
	}
	.info .mid .phone{
		font-size: 3.8vw;
	}
	.info  .icon-fanhui{
		margin-right: 4vw;
	}
	.menu3s{
		background-color: white;
		display: flex;
		align-items: center;
		border-bottom: 1px solid gainsboro;
	}
	.menu3s .item{
		height: 50px;
		width: 33.3%;
		padding-top: 10px;
		text-align: center;
	}
	.menu3s .item .h{
		font-size: 18px;
		
	}
	.menu3s .item .b{
		font-size: 12px;
	}
	.menu3s .line{
		position: relative;
	}
	.menu3s .line:after{
		position: absolute;
		content: '';
		right: 0;top: 0;
		width: 0.4vw;
		height: 50px;background-color: gainsboro;
	}
	
	.bottomBtn{
		
		position: fixed;
		bottom: 28vw;
		width: 90vw;
		text-align: center;
		margin: auto;
		left: 0;right: 0;padding: 3vw;font-size: 4.2vw;
		background-color: orangered;
		color: white;
	}
</style>


