import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '../pages/home/home'
import Order from '../pages/Order/order'
import Person from '../pages/person/person'
import Search from '../pages/search/search'
import Login from '../pages/login/login'
import FoodD from '../pages/foodDetail/foodD'
Vue.use(Router)

export default new Router({
  routes: [
    
    {
    	path:'/home',
    	component:Home,
    	meta:{
    		showFoot:true
    	}
    },
    {
    	path:'/order',
    	component:Order,
    	meta:{
    		showFoot:true
    	}
    },
    {
    	path:'/foodD',
    	component:FoodD,
    	
    },
    {
    	path:'/person',
    	component:Person,
    	meta:{
    		showFoot:true
    	}
    },
    {
    	path:'/search',
    	component:Search,
    	meta:{
    		showFoot:true
    	}
    },
    {
    	path:'/login',
    	component:Login
    },
    {
      path: '/',
      redirect:'/home'
    },
  ]
})



