<template>
	<div>
		<myhead title='搜索'>
			<!--<div class=" left iconfont icon-sousuo" slot='left'>
			</div>-->
			<div class="right mui-icon mui-icon-refresh" slot='right'  @click="refresh()"></div>
		</myhead>
		
		<div class="ophead"></div>
		<div class="searchBox " :class="fadeTop?'fadeTopAct':''" v-if="!shops.length>0">
			<input type="text" class="" placeholder="搜索餐厅名" v-model="shop_name"/>
			<div class="go" @click="search()">Go</div>
		</div>
		<div v-if="shops.length>0">
			<div class="item" v-for="(item,index) in shops" @click="setShop(item)">
				<div class="iDiv">
					<img :src="url+item.shop_img" />
				</div>
				
				<div class="text">
					<div class="name">{{item.shop_name}}</div>
					<div class="t">{{item.shop_activity}}</div>
				</div>
			</div>
		</div>
		
		<div class="wrapper">
			<ul class="content">
				
			</ul>
		</div>
	</div>
</template>

<script>
	import myhead from '../../components/myhead'
	 
	import {seachShop,turl} from '../../api/api'
	import BScroll from 'better-scroll'
	export default {
		components:{
			myhead
		},
		data(){
			return{
				shop_name:'',
				fadeTop:false,
				shops:[]
			}
		},
		methods: {
			async search(){
				var tt=this
			 	 var shops=await seachShop(this.shop_name)
			 	 
			 	 console.log(shops.shop)
			 	 if(shops.shop.length>0){
			 	 	this.fadeTop=true
			 	 	setTimeout(function(){
			 	 		tt.shops=shops.shop
			 	 		tt.fadeTop=false
			 	 	},280)
			 	 }else{
			 	 	mui.alert('无此餐厅')
			 	 }
			 },
			 setShop:function(item){
				//:to='"/foodD?shop_id="+item.shop_id'
				var st=this.$store.state.shop_store
				 
      			st.shop=item
      			console.log(st)
      			this.$router.push('/foodD?shop_id='+item.shop_id)
			},
			refresh(){	
				location.reload()
			}
		},
		computed:{
			url(){
				return turl()[1]
			}
		},
		mounted() {

		}
	}
</script>

<style scoped>
	.mui-icon-refresh{
		line-height: 10vw;font-size: 6vw;
	}
	.searchBox{
		background-color: transparent;
		padding: 0 5px;
		margin: 10px auto;
		width: 95vw;
		position: fixed;top: 35%;left: 0;right: 0;
	}
	.searchBox input{
		margin: 0;
		width: 100%;border-radius: 30px;height: 40px;
	}
	.searchBox .go{
		background-color: white;width: 50vw;margin: 10vw auto;
		height: 35px;line-height: 35px; text-align: center;background-color: deepskyblue;color: white;border-radius: 20px;
	}
	
	.fadeTopAct{
		animation: 0.3s fadeTop;
	}
	.wrapper{
		height: 80vh;
		/*position: absolute;*/  
		/*left: 0;*/  
		/*top: 20vw;*/  
		/*overflow: hidden;*/ 
	}
	@keyframes fadeTop{
		from{opacity: 1; }
		to{opacity: 0;transform: translateY(-150%);}
	}
	.item{padding: 10px;background-color: white;border-bottom: 1px solid gainsboro;display: flex;align-items: center;}
	.item .iDiv{width:45px;height: 45px;overflow: hidden;position: relative;background-color: darkgray;}
	.item .iDiv img{position: absolute;left: 0;right: 0;top: 0;bottom: 0;margin: auto;width: 45px;}
	.item .text{height: 45px;overflow: hidden;padding: 3px 10px;}
	.item .text .name{font-size: 18px;padding-bottom: 8px;}
	.item .text .t{font-size: 12px;color: gray;}
</style>


