<template>

	<div class="goods">
		<span id="" style="display: none;">
			{{allPrice}}
		</span>
		<div class="left left_wrap ">
			<ul class="content">
				<li v-for="(item,index) in goods" :class="currentIndex==index?'active':''" @click="toType(index)">{{item.type}}</li>
			</ul>
			 
		</div>
		<div class="right right_wrap">
			<div class="content">
				<ul v-for="(item,index) in goods" ref='goods' class="goodstype">
					<div class="typehead"  ref='typebar'>{{item.type}}</div>
					<li v-for="(item1,index) in item.food" class="thing" ref='thing'>
						{{item1.food_name}}
						<div class="imgDiv">
							<img v-lazy="aurl+item1.food_img" @click="toShow(item1)"/>
						</div>
						
						
						<div class="price">￥{{item1.food_price}}</div>
						<aos :food='item1'></aos>
					</li>
					<div class="line"></div>
				</ul>
			</div>
		</div>

		<div class="bigPic" v-if="showBig">
			<div class="mask" @click="hideM"></div>
			<div class="thing1">
				<div style="position: relative;width: 60vw;height: 60vw;border-radius: 8px;overflow: hidden;">
					<img  :src="aurl+tempItem.food_img"/>
				</div>
				
				<span class="name">{{tempItem.food_name}}</span>
				<span class="price">￥{{tempItem.food_price}}元</span>
			</div>
		</div>
	</div>

</template>

<script>
	import BScroll from 'better-scroll'
	import aos from '../components/aos'
	import $ from 'jquery'
	import { getFoods, turl } from '../api/api'
	  
	export default {
		data() {
			return {
				scrollY: 0,
				tops: [],
				foods: [],
				goods: [],
				index: 0,
				scrollY:0,
				tops:[],
				activThis:1,
				scroll1:'',
				scroll2:'',
				showBig:false,
				tempItem:{}
			}
		},
		props: [
			'shopID'
		],
		components:{
			aos
		},
		methods: {
			toShow(item){

				this.showBig=true
				this.tempItem=item
			},
			hideM(){
				this.showBig=false
				
			},
			getfoods: function() {
				var tt = this
				getFoods(this.shopID).then((res) => {
					tt.foods = res.food

					
					var temp = []
					for(var i = 0; i < res.type.length; i++) {
						tt.goods.push({
							type: res.type[i].food_type,
							food: []
						})
						temp = res.food.filter(function(item) {
							if(item.food_type == res.type[i].food_type) {
								return item
							}
						})
						tt.goods[i].food = temp
					}
					setTimeout(function() {

						tt. scroll1 = new BScroll('.left_wrap',{
							click:true
						})
						tt. scroll2 = new BScroll('.right_wrap', {
							probeType:3,
							click:true
						})
						
						function onScroll(pos) {
//							console.log(`Now position is x: ${pos.x}, y: ${pos.y}`)
							tt.scrollY=Math .abs(pos.y)
						}
						tt.scroll2.on('scroll', onScroll)
						tt._initTops()
					}, 50)
					
//					/**/console.log(tt.goods)
					tt.$store.state.gouwuche.foods=tt.goods
				})
			},
			_initTops(){
				var tops=[]
				var top=0

				tops.push(top)
				var lis=document.getElementsByClassName('goodstype')
				var arr_lis = Array.prototype.slice.call(lis)


				Array.prototype.slice.call(lis).forEach(li=>{
					top+=li.clientHeight

					tops.push(top)
				})
				this.tops=tops

			},
			toType(index){
				console.log(index)
				this.scroll2.scrollTo(0,-this.tops[index],250)
			}
		},
		computed:{
			currentIndex:function(){
				var {scrollY,tops} = this
				 
				var index=tops.findIndex((top,index)=>{
					 if(scrollY>=top&&scrollY<tops[index+1]){

						return parseInt(index)
					}
				})

				if(index==-1){
					index=0
				}
				return parseInt(index)
			},
			foodChange(){

			},
			aurl(){

				return turl()[1]
			},
			allPrice(){
				var price=0
				var selectedFood=[]
				for (var i=0;i<this.goods.length;i++) {
					for(var j=0;j<this.goods[i].food.length;j++){
						if(this.goods[i].food[j].count){
							price = price + parseFloat(this.goods[i].food[j].count) * parseFloat(this.goods[i].food[j].food_price)
							selectedFood.push(this.goods[i].food[j])
						}
					}
				}
				this.$emit('allprice',{price,selectedFood})

				return price
			}
		},
		mounted() {
			this.getfoods()
			
		}
	}
</script>

<style scoped>
	.goods {
		position: relative;
		height: calc(100vh - 50vw);
	}
	
	.goods .left {
		position: absolute;
		width: 30vw;
		left: 0;
		top: 0;
	}
	
	.goods .left li {
		text-align: center;
		padding: 4vw 0;
		background-color: whitesmoke;
		border-top: 1px solid gainsboro;
	}
	.goods .left .active{
		background-color: white;
	}
	.goods .right {
		position: absolute;
		width: 70vw;
		left: 30vw;
		top: 0;
		background-color: white;
	}
	.goods .right .imgDiv{position: relative;width: 20vw;height: 20vw;overflow: hidden; }
	.goods .right .title {
		display: block;
		font-size: 3vw;
		padding: 2vw 0;
	}
	
	.goods .right .thing {
		min-height: 20vw;
		position: relative;
	}
	
	.goods .right .thing .price {
		position: absolute;
		right: 3vw;
		top: 2vw;
		color: red;
	}
	.goods .right .thing img {
		display: block;position: absolute;left: 0;right: 0;top: 0;bottom: 0;width: 100%;margin: auto;
	}
	.goods .right li {
		display: block;
		font-size: 4vw;
		padding: 2vw 0;
		width: 70vw;
		padding-left: 2vw;
		border-top: 1px solid gainsboro;
		position: relative;
	}
	
	.line {
		height: 1px;
		background-color: whitesmoke;
	}
	
	.left_wrap {
		height: calc(100vh - 50vw);
		position: relative;
		overflow-y: hidden;
	}
	
	.left_wrap ul {
		width: 30vw;
	}
	
	.left_wrap .content {
		position: absolute;
		top: 0;
	}
	
	.right_wrap {
		height: calc(100vh - 50vw);
		position: relative;
		overflow-y: hidden;
	}
	
	.right_wrap .content {
		position: absolute;
		top: 0;
	}
	
	.typehead {
		background-color: whitesmoke;
		color: black;
		font-size: 3.5vw;
		line-height: 7vw;
		padding-left: 2vw;
	}
	.mask{position: fixed;left: 0;right: 0;top: 0;bottom: 0;opacity: 0.7;background-color: black;transition-duration: 0.3s;}
	.bigPic .thing1{position: fixed;top: 30%;left: 0;right: 0;width: 60vw;margin: auto;border-radius: 8px;background-color: white;z-index: 7;}
	.bigPic .thing1 img{display: block;border-radius: 8px;position: absolute;top: 0;left: 0;right: 0;bottom: 0;margin: auto;width: 100%;}
	.bigPic .thing1 .name{font-size: 18px;font-weight: bold;margin-right: 20px;margin: 10px;display: inline-block;}
	.bigPic .thing1 .price{font-size: 14px;color: gray;line-height:40px;display: inline-block;text-align: right; position: absolute;right: 10px;}
</style>


