//vuex

import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex);

import person_store from './person_store.js';//引入某个store对象
import shop_store from './shop_store.js';//引入某个store对象
import gouwuche from './gouwuche.js';//引入某个store对象
export default new vuex.Store({
    modules: {
        person_store,
        shop_store,
        gouwuche
    }
})


