<template>
	<div class="head">
		<slot name='left'></slot>
		<div class="center">{{title}}</div>
		<slot name='right'></slot>
	</div>
</template>

<script>
	export default{
		props:{title:String}
	}
</script>

<style scoped>
	.head{
		line-height: 10vw;
	}
</style>


