import $ from 'jquery'

var url = []
var iUrl=[]
url[0] = 'http://119.23.38.60:6061/'  //http://192.168.0.128:6062/
url[1]='http://119.23.38.60:6061'
iUrl[0]='http://119.23.38.60:6061/'
iUrl[1]='http://119.23.38.60:6061'
//export url
export function turl (){
	return url
}


export  function login(phone, pass) {
	return   new Promise((resolve, rej) => {
		$.ajax({
			type: "post",
			url: url[0] + "login",
			async: true,
			data: {
				phone: phone,
				pass: pass
			},
			success: function(data) {
//				console.log(data)
				resolve(data)
			},
			error: function(err) {
				console.log(err)
			}
		});
	})

}

