<template>
	<div >
		<mhead title='登录'>
			<div class=" left iconfont icon-fanhui" slot='left' onclick="history.go(-1)">
			</div>
		</mhead>
		<div class="ophead"></div>
		<div class="scaleBig">
			<h1 class="title">超级饿</h1>
			<div class="mui-input-group">
				<div class="mui-input-row">
					<label>手机号</label>
					<input type="number" placeholder="请输入手机号" v-model="phone">
				</div>
				<div class="mui-input-row">
					<label>密码</label>
					<input type="text" placeholder="请输入登录密码" v-model="pass">
				</div>
			</div>
			<p class="tip">温馨提示：此登录为登录注册二合一，如果为第一次登录则自动注册并登录。</p>
			<button class="myBtn" @click="login">登录</button>
		</div>
		
		<loading v-if='isLoad'></loading>
		
	</div>
</template>

<script>
	import mhead from '../../components/myhead'
	import loading from '../../components/loading'
	import { login } from '../../api/api'
	export default {
		components: {
			mhead,
			loading
		},
		data() {
			return {
				phone: '',
				pass: '',
				isLoad:false
			}
		},
		methods: {
			login() {
				var tt = this
				tt.isLoad=true
				if(tt.phone.length<11||tt.phone.length>11){
					alert('请输入正确手机号')
				}else{
					login(tt.phone, tt.pass)
					.then(function(data) {
						console.log(data, 'inside')
						tt.isLoad=false
						if(data.data=='ok'){
							tt.$store.state.person_store.is_login=true
							tt.$store.state.person_store.phone=tt.phone
							tt.$router.replace('/person')
						}else if(data.data=='new phone'){
							tt.$store.state.person_store.is_login=true
							tt.$store.state.person_store.phone=tt.phone
							
							tt.$router.replace('/person')
						}else{
							alert('密码错误')
						}
					})
				}
				

			}
		},
		mounted() {
		 
			//			console.log(murl.url[0],"login")
		}
	}
</script>

<style scoped>
	.title {
		margin-top: 14vw;
		text-align: center;
		font-size: 10vw;
		color: dodgerblue;
		margin-bottom: 8vw;
	}
	
	.tip {
		padding: 2vw;
		font-size: 3vw;
		margin-bottom: 2vw;
	}
	
	.myBtn {
		font-size: 4.2vw;
		background-color: dodgerblue;
		color: white;
		width: 80vw;
		margin: auto;
		display: block;
	}
</style>


