<template>
	<div class="aos">
		<transition name='move'>
			<span class="min" v-if="food.count" @click="updateCount(false)">-</span>
		</transition>
		
		<span class="num" v-if="food.count">{{food.count}}</span>		
		<span class="add" @click="updateCount(true)">+</span>
	</div>
</template>

<script>
	export default{
		props:{
			food:Object 
		},
		methods:{
			updateCount(isAdd){
//				console.log(this.$store)
				this.$store.dispatch('updateCount',{isAdd,food:this.food})
			}
		}
	}
</script>

<style scoped>
	  .aos{
		position: absolute;right: 10px;bottom:10px;
	}
	  .aos .min,.add{
		display: inline-block;text-align: center;
		width: 25px;height: 25px;line-height: 25px;border-radius: 50%;background-color: dodgerblue;color: white;
	}
	.move-enter-active,.move-leave-active{
		transition: all 0.3s
	}
	.move-enter,.move-leave-to{
		opacity: 0;
		transform: translateX(15px) rotate(150deg);
	}
</style>


