		<div class="shops">
			<div class="mlabel">
				<span class="iconfont icon-shouye"></span>
				<span>商家</span>
			</div>
			<div class="item" v-for="(item,index) in shops"   @click='setShop(item)'>
				<div class="imgDiv">
					<img   v-lazy='theUrl[1]+item.shop_img'/>
				</div>
				
				<div class="title">{{item.shop_name}}</div>
				<div class="likes">{{item.shop_likes}}赞，月售{{item.shop_sell}}单</div>
				<div class="fee">配送费{{item.shop_fee}}元</div>
				<div class="distance">位置：{{item.shop_address}}</div>
				<div class="tips">{{item.shop_activity}}</div>
			</div>
			<!--<div class="item">
				<img src="../../../build/logo.png" />
				<div class="title">脆皮鸡</div>
