<script>
	import myhead from '../../components/myhead'
	import {getShop,turl} from '../../api/api'
	export default {
		data(){
			return {
				shops:[]
			}
		},
		props:{
			
		},
		computed:{
			theUrl:function(){
				console.log(turl())
				return turl()
			}
		},
		components:{
			myhead
		},
		methods: {
			initSwipter: function() {
				var swiper = new Swiper('.swiper-container', {
					slidesPerView: 1,
					spaceBetween: 30,
					freeMode: true,
					pagination: {
						el: '.swiper-pagination',
						clickable: true,
					},
				});
			},
			initShop:function(){
				var tt=this
				getShop().then((res)=>{
//					console.log(res.data)
					tt.shops=res.data
					console.log(tt.shops)

				})
			},
			setShop:function(item){
				//:to='"/foodD?shop_id="+item.shop_id'
				var st=this.$store.state.shop_store
				 
      			st.shop=item
      			console.log(st)
      			this.$router.push('/foodD?shop_id='+item.shop_id)
			}
		},
		mounted() {
			 
			this.initSwipter()
			this.initShop()
		}
	}
</script>

<style scoped>
	.lunbo{
		background-color: white;
	}
	.row {
		display: flex;
		align-items: center;
		margin-top: 2vw;
	}
	
	.row .col-xs-4 {
		font-size: 4vw;
		text-align: center;
	}
	
	.row .col-xs-4 img {
		width: 18vw;
		display: block;
		margin: auto;
		margin-bottom: 1vw;
	}
	
	.swiper-container {
		padding-bottom: 5vw;
	}
	
	.shops{
		background-color: white;margin-bottom: 18vw; 
	}
	.shops .mlabel{
		padding: 2vw 1vw 2vw 3vw;
		color: gray;
		
	}
	.shops .item {
		position: relative;
		width: 100vw;
		border-bottom: 1px solid gainsboro;
		min-height: 24vw;
	}
	.shops .item .imgDiv{
		position: relative;
		width: 20vw;
		height: 24vw;
		overflow: hidden;
		left: 2vw;
		
	}
	.shops .item img {
		width: 100%;
		position: absolute;
		top: 0;left: 0;right: 0;bottom: 0;
		 margin: auto;display: block;
	}
	
	.shops .item .title {
		font-size: 4.2vw;
		color: black;
		font-weight: 500;
		position: absolute;
		top: 2vw;
		left: 26vw;
	}
	
	.shops .item .fee {
		font-size: 3vw;
		color: gray;
		position: absolute;
		top: 12.5vw;
		left: 26vw;
	}
	
	.shops .item .likes {
		font-size: 2.8vw;
		color: gray;
		position: absolute;
		top: 8vw;
		left: 26vw;
	}
	
	.shops .item .distance {
		font-size: 3vw;
		color: gray;
		position: absolute;
		top: 17vw;
		left: 26vw;
	}
	
	.shops .item .tips {
		font-size: 4vw;
		color: orangered;
		position: absolute;
		top: 10vw;
		right: 5vw;
	}
</style>


