import Vue from 'vue'

export default{
	state:{
		foods:{}
	},
	actions:{
		updateCount({commit},{isAdd,food}){
			if(isAdd){
//				console.log('+')
				commit('addCount',{food})
			}else{
//				console.log('-')
				commit('subCount',{food})
			}
		}
	},
	mutations:{
		addCount(state,{food}){
			if(!food.count){
//				food.count=1
				Vue.set(food,'count',1)
//				console.log(food.count,'c')
			}else{
				food.count++
			}
		},
		subCount(state,{food}){
			if(!food.count){
//				console.log(food.count)
				food.count=0
			}else{
				food.count--
			}
		}
	}
}



