<template>
	<div>
		<myhead title='订单'>
			<!--<div class=" left iconfont icon-sousuo" slot='left'></div>-->
			 
		</myhead>
		
		<div class="ophead"></div>
		<div v-if="isLogin" class="mcontent">
			<h2 class="title">历史订单</h2>
			<ul>
				<li class="item" v-for="(item,index) in orders">
					<img v-lazy='url+item.shop_img' />
					<div class="shop">搜索</div>
					<div class="time">未知</div> 
					<div class="things">
						<div v-for="(item1,index) in item">{{item1.food_name}} &nbsp;&nbsp;*{{item1.count}}</div>
					</div>
					<div class="price">￥{{prices[index]}}</div>
				</li>
			</ul>
		</div>
		<div v-if="!isLogin" class="mcontent">
			<span>登陆后查看</span>
		</div>
	</div>
</template>

<script>
	import myhead from '../../components/myhead'
	import {getOrders,turl} from '../../api/api'
	export default {
		components:{
			myhead
		},
		data(){
			return {
				 orders:[],
				 prices:[]
			}
		}
		,
		methods: {
			async getInfo(){
				if(this.$store.state.person_store.phone!=''){
					var tt=this
				 	var res= await getOrders(this.$store.state.person_store.phone)
				 	var things=[]
				 	res.map(function(item,index){
				 		tt.orders.push(JSON.parse(item.order_thing))
				 		tt.prices.push(item.order_price)
				 	})
				 	 
				 	console.log(res)
				}
				
			 }
		},
		computed:{
			isLogin(){
				return this.$store.state.person_store.is_login
			},
			url(){
				return turl()[1]
			}
		},
		mounted() {
			 this.getInfo()
		}
	}
</script>

<style  scoped>
	.mcontent{padding: 2vw;background-color: white;min-height: 100vh;padding-bottom: 16vw;}
	.mcontent>.title{
		font-size: 5.5vw;margin-top: 1vw;margin-bottom: 2vw;
	}
	.mcontent .item img{
		position: absolute;left: 2vw;top: 2vw;width: 10vw;height: 10vw;
	}
	.mcontent .item{position: relative;padding: 2vw;border-bottom: 1px solid gainsboro;}
	.mcontent .item .shop{
		font-size: 4vw;font-weight: bold;margin-left: 12vw ;line-height: 5.3vw;
	}
	.mcontent .item .time{
		font-size: 3vw; margin-left: 12vw ;color: gray;
	}
	.mcontent .item  .price{position: absolute;right: 2vw;font-size: 4.5vw;top: 2vw;color: dodgerblue;}
	.mcontent .item .things{
		margin-top: 5vw;font-size: 4vw;margin-left: 12vw;
	}
</style>


